/* vim: set ft=stylus: */

.Spinner {

  /* @mixin contentBlock;*/
  /* padding: 1em 0;*/
  /* $localSpinnerColor: $primaryColor;*/

  position: relative;

}

.Spinner-Rotor {
    margin: 0 auto;
    width: 100px;
    height: 100px;
    position: relative;
    /* opacity: .5;*/
  }

.Spinner-Circle {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border: 2px solid #999;
    border-radius: 50%;
    -webkit-animation-name: Spinner;
            animation-name: Spinner;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

@-webkit-keyframes Spinner {
    0% {
      width: 0%;
      height: 0%;
      opacity: 0;
    }
    20% {
      width: 30%;
      height: 30%;
      opacity: .4;
    }
    40% {
      width: 50%;
      height: 50%;
      opacity: .6;
    }
    100% {
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }

@keyframes Spinner {
    0% {
      width: 0%;
      height: 0%;
      opacity: 0;
    }
    20% {
      width: 30%;
      height: 30%;
      opacity: .4;
    }
    40% {
      width: 50%;
      height: 50%;
      opacity: .6;
    }
    100% {
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }




