/* vim: set ft=stylus: */
.Header {
  -webkit-transition: all 500ms;
  transition: all 500ms;
  z-index: 10;
  border: 0px solid transparent;
  opacity: .8;

  /* &:hover {*/
  /*   opacity: .9;*/
  /* }*/

  -webkit-animation-name: HeaderAppear;

          animation-name: HeaderAppear;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;

}
/* padding: 10px;*/
.Header-Container {
    padding: 10px;
    /* margin: 0 10px;*/
    background-color: #c20;
    text-align: center;
  }
@media (min-width: 576px) {
.Header {
    width: 100%;
    position: fixed;
    top: 0;
    /* margin: 10px auto;*/
    /* padding: 0 10px;*/
    border: 15px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box

}
    .Header-Container {
      height: $(headerHeight - 15 * 2)px;
      margin: 0 auto;
      display: table;
      padding-left: 40px;
      padding-right: 40px;
      border-radius: 50px;
    }
    .Header-Logo,
    .Header-Menu {
      padding: 0 15px;
      display: table-cell;
      vertical-align: middle;
    }
    .Header-Logo {
      height: 60px;
    }
    .Header-Menu {
      white-space: nowrap;
    }
  }
@media (min-width: 576px) {
.Header:not(.Header_mode_home) .Header-Container {
      min-width: 456px
  }
    }
@media (min-width: 768px) {
.Header:not(.Header_mode_home) .Header-Container {
      min-width: 648px
  }
    }
@media (min-width: 992px) {
.Header:not(.Header_mode_home) .Header-Container {
      min-width: 872px
  }
    }
@media (min-width: 1200px) {
.Header:not(.Header_mode_home) .Header-Container {
      min-width: 1080px
  }
    }
/* Home page (at middle)...*/
.Header_mode_home {
    width: 100%;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    /* opacity: .85;*/
  }
@-webkit-keyframes HeaderAppear {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0;
    }
    100% {
      opacity: .8;
    }
  }
@keyframes HeaderAppear {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0;
    }
    100% {
      opacity: .8;
    }
  }



