/* vim: set ft=stylus: */

.PageAnim-Show {

    position: relative;
    width: 100%;

  }

/* overflow: hidden;*/

.PageAnim-Show_entering {
      position: relative;
      /* top: -$transition-offet;*/
      opacity: 0;
      -webkit-transition: opacity 1000ms ease-in;
      transition: opacity 1000ms ease-in;
    }

.PageAnim-Show_entering.PageAnim-Show_enteringActive {
        /* top: 0px;*/
        opacity: 1;
      }

.PageAnim-Show_exiting {
      position: absolute;
      /* top: 0;*/
      /* border: 1px solid red;*/
      opacity: 1;
      -webkit-transition: opacity 1000ms ease-out;
      transition: opacity 1000ms ease-out;
    }

.PageAnim-Show_exiting.PageAnim-Show_exitingActive {
        opacity: 0;
        /* top: 20px;*/
      }



