/* vim: set ft=stylus: */

.Header-LogoImg {
      display: block;
      max-height: 60px;
      width: 300px;
      max-width: 100%;
      margin: 0 auto;
      -webkit-transition: opacity .15s;
      transition: opacity .15s;
      opacity: .75;
    }

.Header:not(.Header_mode_home) .Header-LogoImg {
        cursor: pointer;
        opacity: .6;
      }

.Header:not(.Header_mode_home) .Header-LogoImg:hover {
          opacity: .85;
        }



