/* vim: set ft=stylus: */
.LoadedPage {
  width: 100%;
  /* animation: LoadedPageEnter $(pageTransitionTimeout)ms linear;*/
}
@-webkit-keyframes LoadedPageEnter {
  from {
    position: absolute;
    opacity: 0;
  }
  to {
    position: relative;
    opacity: 1;
  }
}
@keyframes LoadedPageEnter {
  from {
    position: absolute;
    opacity: 0;
  }
  to {
    position: relative;
    opacity: 1;
  }
}




