/* vim: set ft=stylus: */

/* Content...*/

.Main-Content .Facepalm {
    /* @mixin clearfix;*/
    position: relative;
  }

.Main-Content .Facepalm img {
      float: right;
      margin-left: 15px;
      margin-top: -50px;
      border-radius: 50%;
      width: 120px;
      height: 120px;
    }

@media (min-width: 768px) {

.Main-Content .Facepalm img {
        width: 160px;
        height: 160px
    }
      }

@media (min-width: 1200px) {

.Main-Content .Facepalm img {
        width: 200px;
        height: 200px
    }
      }

.Main-Content code:not(.hljs) {
    display: inline-block;
    background-color: rgba(153, 153, 153, 0.1);
    -webkit-box-shadow: inset 0 0 0 1px rgba(153, 153, 153, 0.1);
            box-shadow: inset 0 0 0 1px rgba(153, 153, 153, 0.1);
    border-radius: 3px;
    font-size: 85%;
    margin: 0;
    padding: .15em .5em;
  }

.Main-Content h6,
  .Main-Content h5,
  .Main-Content h4,
  .Main-Content h3,
  .Main-Content h2,
  .Main-Content h1 {
    font-size: 28px;
    font-weight: 300;
    color: #c20;
    font-size: 20px;
    line-height: 1.2;
  }

.Main-Content h6:first-child, .Main-Content h5:first-child, .Main-Content h4:first-child, .Main-Content h3:first-child, .Main-Content h2:first-child, .Main-Content h1:first-child {
      margin-top: 0;
      padding-top: 0;
    }

.Main-Content h1 {
    font-size: 36px;
  }

.Main-Content h2 {
    font-size: 28px;
  }

.Main-Content h3 {
    font-size: 24px;
  }



