/* vim: set ft=stylus: */
.List {
  list-style: none;
  padding: 0;
  margin: 0;

  position: relative;

}
.List > li {

    /* Space between items*/
    margin-top: .5em;
    margin-bottom: .5em;

    min-height: 25px;
    padding-left: 30px;
    position: relative;

  }
.List > li:before {
      content: "";
      background-size: cover;
      background-image: url('/images/items/bullet-item.svg');
      width: 25px;
      height: 25px;
      position: absolute;
      left: 0;
      opacity: .2;
    }



