/* vim: set ft=stylus: */

.HomePage {

  min-height: 100%;

  background-color: rgba(153, 153, 153, 0.5);

  /* Animated grandient background...*/ /* 217;*/ /* 127;*/ /* 336;*/

  background:
    linear-gradient(197deg, rgba(255,0,0,.8), rgba(0,255,0,0) 87%),
    linear-gradient(137deg, rgba(0,255,0,.8), rgba(0,255,0,0) 91%),
    linear-gradient(324deg, rgba(0,0,255,.8), rgba(0,255,0,0) 53%);

}

.HomePage-Effects {
    opacity: 0;
    -webkit-transition: opacity 1.5s;
    transition: opacity 1.5s;
  }

.HomePage-Effects_ready {
      opacity: 1;
    }

.HomePage-Effect_1,
  .HomePage-Effect_2 {
    position: absolute;
    left:   0;
    right:  0;
    top:    0;
    bottom: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

.HomePage-Effect_1 {
    -webkit-animation-name: HomePageBg1;
            animation-name: HomePageBg1;
    -webkit-animation-duration: 15s;
            animation-duration: 15s;
  }

.HomePage-Effect_2 {
    -webkit-animation-name: HomePageBg2;
            animation-name: HomePageBg2;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
  }

@-webkit-keyframes HomePageBg1 {
    0% {
      opacity: .2;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      opacity: .7;
    }
    100% {
      opacity: .2;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes HomePageBg1 {
    0% {
      opacity: .2;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      opacity: .7;
    }
    100% {
      opacity: .2;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@-webkit-keyframes HomePageBg2 {
    0% {
      opacity: .8;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      opacity: .3;
    }
    100% {
      opacity: .8;
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }

@keyframes HomePageBg2 {
    0% {
      opacity: .8;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      opacity: .3;
    }
    100% {
      opacity: .8;
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }



