/* vim: set ft=stylus: */

/* $galleryAccentColor: $primaryColor;*/

.Gallery {

  margin-top: 1em;

  margin-bottom: 1em;
}

.Gallery > .ReactGridGallery::after {

  content: "";

  display: table;

  clear: both;
}

/* Thumbnails...*/

.Gallery > .ReactGridGallery .tile, .Gallery > .ReactGridGallery .tile .tile-overlay {
        border-radius: 3px;
        -webkit-transition: all .25s;
        transition: all .25s;
      }

/* Tags...*/

.Gallery > .ReactGridGallery .tile .tile-bottom-bar div span {
        color: #fff !important;
        font-weight: 400 !important;
        opacity: .7;
      }

.Gallery > .ReactGridGallery .tile:hover .tile-overlay {
          background-color: rgba(0, 0, 0, 0.1) !important;
          -webkit-box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.2);
                  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.2);
        }

/* Lightbox popup...*/

#lightboxBackdrop {

  padding: 15px;

}

/* Buttons (next, prev, close)*/

#lightboxBackdrop button {
    -webkit-transition: all .15s;
    transition: all .15s;
  }

#lightboxBackdrop button:hover {
      opacity: .6;
    }

/* Thumbnials...*/

#lightboxBackdrop div[class^='thumbnail'] {
    -webkit-transition: all .25s;
    transition: all .25s;
  }

#lightboxBackdrop div[class^='thumbnail']:hover {
      opacity: .5;
    }

/* Active thumbnail...*/

#lightboxBackdrop div[class*='thumbnail__active'] {
    -webkit-box-shadow: 0 0 0 2px rgb(0, 0, 0);
            box-shadow: 0 0 0 2px rgb(0, 0, 0);
    opacity: .6;
  }



